.sign-out-btn{
    background-color: rgba(255, 255, 255, 0.0);
    border: 2px solid black;
    color: black;
    border-radius: 10px;
    transition: 0.3s;
}

.sign-out-btn:hover{
    cursor: pointer;
    background-color: gray;
}

.sign-out-btn:active{
    transform: translateY(5px);
}
