@import url(https://fonts.googleapis.com/css2?family=Lora&family=Otomanopee+One&display=swap);
body{
    background-image: url(/static/media/backgroundOne.7ab92cc1.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 100%;
    font-family: 'Lora', serif;
    font-family: 'Otomanopee One', sans-serif; 
}




h1{
    text-align: center;
    font-size: 50px;
    color: white;
    text-shadow: 5px 5px black;
    padding: 20px;
}

.reminders{
    margin: auto;
    overflow-y: scroll;
    height: 60vh;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.loading{
    text-align: center;
}
/* 
Scrollbar styling
*/

::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
background: rgba(255, 255, 255, 0); 
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
background: #555; 
}

.sign-out-btn{
    background-color: rgba(255, 255, 255, 0.0);
    border: 2px solid black;
    color: black;
    border-radius: 10px;
    transition: 0.3s;
}

.sign-out-btn:hover{
    cursor: pointer;
    background-color: gray;
}

.sign-out-btn:active{
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}

.reminder{
    background-color: rgba(255, 255, 255, 0.5);
    width: 55vw;
    overflow-wrap: break-word;
    border: 2px solid black;
    border-radius: 10px;
    padding: 15px;
    margin: 0 auto;
    margin-bottom: 10px;
}

.info-block{
    display: grid;
    grid-template-columns: 70% auto auto;
    padding: 5px;
}

.edit{
    margin-right: 10px;
    background-color: rgb(0, 225, 0);
    border-radius: 5px;
    border: 2px black solid;
    transition: 0.5s;
}

.delete{
    margin-left: 0px;
    background-color: rgb(255, 0, 0);
    border-radius: 5px;
    border: 2px black solid;
    transition: 0.5s;
}

.edit:hover{
    background-color: rgb(0, 165, 0);
}

.delete:hover{
    background-color: rgb(200, 0, 0);
}

.edit:hover, .delete:hover{
    cursor: pointer;
}

.edit:active, .delete:active{
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}

.reminder > div, span{
    text-align: left;
}

.reminder-name{
    font-size: 25px;
}

span{
    font-size: 20px;
    color: white;
}

@media only screen and (max-width: 900px) {
    /* After the "time" span, input a line break and move the buttons to a new line. */
    .time::after{
        content: "\a";
        white-space: pre;
    }

    /* For smaller displays, unset the grid display to enable the above line break */
    .info-block{
        display: inline;
        display: initial;
        padding: 5px;
    }    

    .reminder{
        width: 80vw;
    }
}

/* Responsiveness for smartphones */
@media only screen and (max-width: 360px) {
     /* After the "time" span, input a line break and move the buttons to a new line. */
    .time::after{
        content: "\a";
        white-space: pre;
    }

    /* For smaller displays, unset the grid display to enable the above line break */
    .info-block{
        display: inline;
        display: initial;
        padding: 5px;
    }    

    .reminder{
        width: 90vw;
    }
}
/*  */
.set-reminder-btn-group{
    display: grid;
    grid-template-columns: 50% 50%;
    width: 35%;
    margin: auto;
}

.is-single-occuring, .is-reoccuring{
    cursor: pointer;
    border: black 2px solid;
    background-color: rgba(211,211,211, 0);
    padding: 10px;
    transition: 0.4s;
}

.is-single-occuring{
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.is-reoccuring{
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.is-single-occuring:hover, .is-reoccuring:hover{
    background-color: black;
    color: white;
}

.is-single-occuring:active, .is-reoccuring:active{
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}

.active{
    background-color: black;
    color: white;
}

@media only screen and (max-width: 900px) {
    .set-reminder-btn-group{
        width: 50%;
    }
}


@media only screen and (max-width: 360px) {
    .set-reminder-btn-group{
        width: 90%;
    }
}
.title{
    color: white;
    text-shadow: 5px 5px black;
    text-align: center;
    font-size: 40px;
    padding: 10px;
}

/* Center the reminder name input, and reminder date/time input */
.reminder-input-div, .reminder-date-div, .reminder-time-div{
    text-align: center;
}

/*  */
.set-reminder-btn{
    display: grid;
    margin: auto;
    margin-top: 40px;
    background-color: rgb(211,211,211);
    font-size: 15px;
    border-radius: 10px;
    border: 2px solid white;
    padding: 15px;
    transition: 0.5s;
}

.set-reminder-btn:hover{
    background-color: rgb(111,111,111);
    cursor: pointer;
}

.set-reminder-btn:active{
    cursor: pointer;
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}
/* End of post button styling */

#reminder-input{
    border-radius: 10px;
    font-size: 20px;
    border: 2px black solid;
}

.label{
    display: block;
    color: rgb(211,211,211);
    font-size: 25px;
}

form{
    margin: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 20px;
}
.select-days-title{
    text-align: center;
    padding: 10px;
}

.days-error{
    text-align: center;
}

.days{
    text-align: center;
    padding: 5px;
}

.day{
    background-color: rgb(211,211,211);
    border-radius: 50%;
    padding: 6px;
    margin: 5px;
    transition: 0.4s;
    color: black;
}

.day:hover{
    background-color: rgb(111, 111, 111);
    cursor: pointer;
}

.day:active{
    background-color: green;
}
.not-found{
    font-size: 50px;
    text-align: center;
    color: white;
}
